.App {
  font-family: var(--principalFont);
  color: white;
}

#circle1 {
  position: absolute;
  left: 150px;
  width: 23%;
}

#circle2 {
  position: absolute;
  right: 180px;
  width: 23%;
}

.header-image__brand {
  width: 100%;
  height: 100%;
}

.header-imagen__mapa {
  /* height: 30px;
  width: 50px; */
}

.section-info__SBRMText{
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.section-info{
  background-color: var(--white);
  margin: 25px;
  
}

.section-info__line1{
  height: 200px;
  width: 25px;
  rotate: 45deg;
  border-radius: 15px;
  position: relative;
  left: -7em;
  top:-9em;
  background-color: var(--dark-blue);
}

.section-info__line2{
  height: 100px;
  width: 25px;
  rotate: 45deg;
  border-radius: 15px;
  position: relative;
  left: -10em;
  top:-11em;
  background-color: var(--dark-blue);
}

.section-info__SBRM, .section-info__country{
  display: flex;
  align-items: flex-start;
  column-gap: 5em;
  margin: 250px 0;
}

.section-info__countryImage{
  z-index: 1;
  position: relative;
  height: 100%;
}

.section-country__image{
  display: flex;
  align-items: center;
}

.section-info__SBRMImage{
  z-index: 1;
  position: relative;
  height: 100%;
  margin-right: 100px;
  margin-left: 150px;
  /* width: 500px;
  height: 500px;
  position: relative;
  top: 29px;
  left: -4em; */
}

.section-info__SBRMTextTitle{
  font-size: 70px;
  margin: 0;
  font-weight: 900;
}

.section-info__SBRMTextParagrah{
  font-size: 16px;
  font-size: 20px;
  font-weight: 500;
}

.info__countryTextTitle{
  font-family: 'Raleway', sans-serif;
}

