.section-info__SBRM{
  color: var(--dark-blue);
}


strong {
  font-family: var(--secundaryFont);
  font-size: 10vw;
}

strong:nth-child(1) {
    color: yellow;
}

strong:nth-child(2) {
    color: var(--dark-blue);
}

strong:nth-child(3) {
    color: red;
}


.section-info__country{
  color: var(--dark-blue);
  align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: var(--light-blue);
  max-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.header__brand > img {
  color: var(--dark-blue)
}

.header__brand > p {
  color: var(--dark-blue)
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 1235px) {
  .header-imagen__mapa {
    display: none;
  }
}



@media (min-width: 1024px) {
  .header__brand {
    margin-left: 170px;
  }
}

@media (max-width: 992px) {
  #header h1 {
    font-size: 36px;
  }
  #header h2 {
    font-size: 20px;
    line-height: 30px;
  }
  #header .social-links {
    margin-top: 15px;
  }
  #header .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media (min-width: 768px) and (max-width: 992px){
  .section-info__SBRM, .section-info__country{
    flex-direction: column;
    /* row-gap: 5em; */
  }
  #circle1, #circle2 {
    width: 50%;
  }
  /* #circle2{
    top: 216em;
  } */

  .header-imagen__mapa {
    display: none;
  }
  .header__brand {
    margin: 170px 0;
  }
  .section-info__SBRMText {   
    align-items: center;
  }
}

@media (max-width: 768px) {
  .section-info__SBRM, .section-info__country{
    flex-direction: column;
    /* row-gap: 5em; */
  }
  #circle1, #circle2 {
    width: 50%;
  }
  /* #circle2{
    top: 216em;
  } */
  strong {
    font-size: 15vw;
  }
  .header-imagen__mapa {
    display: none;
  }

  .header__brand {
    margin: 170px 0;
  }

.section-info__SBRMText {   
  align-items: center;
}

}

